/* COMING SOON PAGE*/
.coming-soon .layer{
	width:100%;
	height:100%;
	display:block;
}
.coming-soon{
	position:absolute;
	width:100%;
	height:100%;
	text-align:center;
	padding-top:155px;

}
.coming-soon .welcome-text{
	font-size:60px;
	font-weight:normal;
}
.coming-soon .welcome-description{
	font-size:16px;
	padding-top:5px;
	font-weight:normal;
}
.coming-soon .title{
	border: 2px solid;
	padding: 8px 15px;
	display:inline-block;
	margin-bottom:7px;
	font-size:30px;
	text-transform:uppercase;
	font-weight:normal;
}
.coming-soon #scene{
	width:100%;
	height:100%;
	background:url("../images/bg-welcome.jpg") repeat scroll top center / cover  rgba(0, 0, 0, 0);
	position:fixed;
	width:100%;
	height:100%;
	z-index:-1;
}
.coming-soon .layer-img{
	width:100%;
	height:100%;
	background:url("../images/bg-welcome.jpg") repeat scroll top center / cover  rgba(0, 0, 0, 0);
	opacity:0.3;
}
.coming-soon .layer .layer-img{
	opacity:0.3;
}
.coming-soon h6{
	margin-top:130px;
}
.coming-soon .fa-heart{
	color:#ef333d;
}
.countdown-section{
	width:113px;
	height:113px;
	border:1px solid rgba(30,30,30,0.2);
	background:rgba(0,0,0,0.2);
	color:#fff;
	margin:30px 12px 0 12px;
	display:inline-block;
	font-size:18px;
	text-transform:uppercase;
}
.countdown-section .countdown-amount{
	font-size:48px;
	display:block;
	text-align:center;
	line-height:48px;
	margin:15px 0 3px 0;
}


