/*---------------------------------------------------
	HEADER
------------------------------------------------------*/
.header {
  margin-bottom: 0;
}

#header {
  position: fixed;
  top: 0;
}

#header.reveal:not(.alt) {
  box-shadow: 0 5px 8px 5px rgba(0, 0, 0, 0.2);
  position: fixed;
  width: 100%;
  top: 0;
}

#header.reveal:not(.alt) {
  -webkit-animation: reveal-header 1s cubic-bezier(0.005, 0.975, 0.73, 1);
  -moz-animation: reveal-header 1s cubic-bezier(0.005, 0.975, 0.73, 1);
  -ms-animation: reveal-header 1s cubic-bezier(0.005, 0.975, 0.73, 1);
  -o-animation: reveal-header 1s cubic-bezier(0.005, 0.975, 0.73, 1);
  animation: reveal-header 1s cubic-bezier(0.005, 0.975, 0.73, 1);
  border: 1px solid transparent;
  background: #0f0f0f;
}

.header {
  position: relative;
  width: 100%;
  z-index: 9999;
  background: #0f0f0f;
}

.header.header-container {
  background: none repeat scroll 0 0 transparent;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 999;
  margin: 0 auto;
}

.page-category .header-container {
  background: none repeat scroll 0 0 #0f0f0f;
}

/*-- Logo --*/
.logo {
  padding: 0 !important;
}

.header-container .logo a {
  display: inline-block;
  margin: 30px 10px;
}

/*-- Navigation --*/
/* mobile menu */
.menu-wrap {
  border: 1px solid transparent;
  background: #ec3642;
  position: fixed;
  width: 270px;
  height: 100%;
  transition: transform ease 1s;
  -o-transition: -o-transform ease 1s;
  -ms-transition: -ms-transform ease 1s;
  -moz-transition: -moz-transform ease 1s;
  -webkit-transition: -webkit-transform ease 1s;
  transform: translateX(270px);
  -o-transform: translateX(270px);
  -ms-transform: translateX(270px);
  -moz-transform: translateX(270px);
  -webkit-transform: translateX(270px);
  right: 0;
  top: 0;
  margin-right: -18px;
  z-index: 999999;
  overflow-y: scroll;
  padding: 0 15px;
}

.show-menu .menu-wrap {
  transition: all ease 0.6s;
  -o-transition: -o-transform ease 0.6s;
  -ms-transition: -ms-transform ease 0.6s;
  -moz-transition: -moz-transform ease 0.6s;
  -webkit-transition: -webkit-transform ease 0.6s;
  transform: translateX(0);
  -o-transform: translateX(0);
  -ms-transform: translateX(0);
  -moz-transform: translateX(0);
  -webkit-transform: translateX(0);
}

.content-wrapper:before {
  background: rgba(0, 0, 0, 0.5);
  content: "";
  height: 0;
  position: absolute;
  right: 0;
  top: 0;
  opacity: 0;
  transition: opacity 0.5s ease 0s;
  -o-transition: opacity 0.5s ease 0s;
  -ms-transition: opacity 0.5s ease 0s;
  -moz-transition: opacity 0.5s ease 0s;
  -webkit-transition: opacity 0.5s ease 0s;
  width: 0;
  z-index: 10000;
}

.show-menu .content-wrapper:before {
  height: 100%;
  opacity: 1;
  transition: opacity 0.5s ease 0s;
  -o-transition: opacity 0.5s ease 0s;
  -ms-transition: opacity 0.5s ease 0s;
  -moz-transition: opacity 0.5s ease 0s;
  -webkit-transition: opacity 0.5s ease 0s;
  width: 100%;
}

.main-menu {
  position: relative;
}

.main-menu h4 {
  margin: 0;
  text-transform: uppercase;
  font-size: 18px;
  padding: 14px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.35);
}

.menu-wrap ul {
  list-style: outside none none;
  padding: 0;
}

.menu-wrap ul.nav-menu > li {
  padding-left: 4px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.35);
}

.menu-wrap li a {
  color: #fff;
  display: block;
}

.menu-wrap ul.nav-menu > li > a {
  font-size: 14px;
  padding: 11px 0;
}

#close-button {
  background: none;
  border: none;
  color: #fff;
  position: absolute;
  right: 0;
  top: 11px;
  font-size: 18px;
}

#open-button {
  border: medium none;
  display: none;
  font-size: 18px;
  height: 40px;
  line-height: 40px;
  position: absolute;
  right: 0;
  text-align: center;
  top: 28px;
  width: 40px;
}

#open-button:before {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  content: '\f0c9';
  font-family: FontAwesome;
  text-align: center;
  color: #fff;
  font-size: 30px;
}

.dropdown a {
  text-decoration: none;
}

.dropdown [data-toggle="dropdown"] {
  position: relative;
  display: block;
  color: #fff;
  padding: 10px;
}

.dropdown .icon-arrow {
  position: absolute;
  display: block;
  font-size: 0.7em;
  color: #fff !important;
  top: 14px;
  right: 10px;
}

.dropdown .icon-arrow.open {
  -moz-transform: rotate(-180deg);
  -ms-transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
  -moz-transition: -moz-transform 0.6s;
  -o-transition: -o-transform 0.6s;
  -webkit-transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
}

.dropdown .icon-arrow.close {
  -moz-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -moz-transition: -moz-transform 0.6s;
  -o-transition: -o-transform 0.6s;
  -webkit-transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  opacity: 1 !important;
}

.dropdown .icon-arrow:before {
  content: '\25BC';
}

.dropdown .dropdown-nav {
  max-height: 0;
  overflow: hidden;
  list-style: none;
  padding: 0;
  margin: 0;
}

.dropdown .dropdown-nav li {
  padding: 0;
}

.dropdown .dropdown-nav li a {
  color: #fff;
  display: block;
  padding: 10px;
}

.dropdown .show, .dropdown .hide {
  -moz-transform-origin: 50% 0%;
  -ms-transform-origin: 50% 0%;
  -webkit-transform-origin: 50% 0%;
  transform-origin: 50% 0%;
}

.dropdown .show {
  display: block;
  max-height: 9999px;
  -moz-transform: scaleY(1);
  -ms-transform: scaleY(1);
  -webkit-transform: scaleY(1);
  transform: scaleY(1);
  animation: showAnimation 0.5s ease-in-out;
  -moz-animation: showAnimation 0.5s ease-in-out;
  -webkit-animation: showAnimation 0.5s ease-in-out;
  -moz-transition: max-height 1s ease-in-out;
  -o-transition: max-height 1s ease-in-out;
  -webkit-transition: max-height 1s ease-in-out;
  transition: max-height 1s ease-in-out;
}

.dropdown .hide {
  max-height: 0;
  -moz-transform: scaleY(0);
  -ms-transform: scaleY(0);
  -webkit-transform: scaleY(0);
  transform: scaleY(0);
  animation: hideAnimation 0.4s ease-out;
  -moz-animation: hideAnimation 0.4s ease-out;
  -webkit-animation: hideAnimation 0.4s ease-out;
  -moz-transition: max-height 0.6s ease-out;
  -o-transition: max-height 0.6s ease-out;
  -webkit-transition: max-height 0.6s ease-out;
  transition: max-height 0.6s ease-out;
}

/* megamenu */
.megamenu {
  text-align: right;
}

.megamenu ul.nav-menu {
  padding-left: 0;
}

.megamenu ul.nav-menu > li {
  display: inline-block;
  float: left;
  margin: 7px 15px;
  position: relative;
}

.boxing-page .megamenu ul.nav-menu > li {
  padding: 7px 15px;
  margin: 0;
}

.megamenu ul > li > a {
  color: #fff;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  margin: 30px 0;
  padding: 0;
  position: relative;
  text-transform: none;
}

.megamenu ul.nav-menu > li :before, .megamenu ul.nav-menu > li:after {
  border-bottom-color: #ec3642;
}

.megamenu ul.nav-menu > li:before {
  right: 50%;
}

.megamenu ul.nav-menu > li:after {
  left: 50%;
}

.megamenu ul.nav-menu > li:hover a, .megamenu ul.nav-menu > li.active {
  color: #ec3642;
}

.megamenu ul.nav-menu > li:before, .megamenu ul.nav-menu > li:after {
  border-bottom: 2px solid #ec3642;
  bottom: 28px;
  content: "";
  position: absolute;
  width: 0;
  transition: width 250ms ease-out 0s;
  -o-transition: width 250ms ease-out 0s;
  -ms-transition: width 250ms ease-out 0s;
  -moz-transition: width 250ms ease-out 0s;
  -webkit-transition: width 250ms ease-out 0s;
}

.boxing-page .megamenu ul.nav-menu > li:before, .boxing-page .megamenu ul.nav-menu > li:after {
  bottom: -1px;
}

.megamenu ul.nav-menu > li:hover:before, .megamenu ul.nav-menu > li:hover:after, .megamenu ul.nav-menu > li.active:before, .megamenu ul.nav-menu > li.active:after, .megamenu ul.nav-menu > li:hover:before, .megamenu ul.nav-menu > li:hover:after {
  width: 50%;
}

.mainnav .nav-menu li .child-nav {
  background-color: #fff;
  border: 1px solid #efefef;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  left: 0;
  top: 76px;
  list-style: outside none none;
  min-width: 240px;
  padding: 10px 18px;
  position: absolute;
  transition: all 0.2s ease 0s;
  -webkit-transition: all 0.2s ease 0s;
  -moz-transition: all 0.2s ease 0s;
  -ms-transition: all 0.2s ease 0s;
  -o-transition: all 0.2s ease 0s;
  opacity: 0;
  visibility: hidden;
  z-index: 999;
}

.boxing-page .mainnav .nav-menu li .child-nav {
  left: 0px;
  top: 115px;
}

.megamenu .nav-menu > li .child-nav:before {
  border-color: transparent transparent transparent #fff;
  border-style: solid;
  border-width: 11px;
  top: -11px;
  content: "";
  left: -1px;
  position: absolute;
}

.megamenu .nav-menu > li:hover > .child-nav {
  opacity: 1;
  visibility: visible;
}

.megamenu .nav-menu > li ul.child-nav li {
  line-height: 24px;
  padding: 6px 0;
}

.megamenu .nav-menu > li ul.child-nav li + li {
  border-top: 1px solid #f2f2f2;
}

.megamenu .nav-menu > li ul.child-nav li a {
  color: #828282;
  display: block;
  margin: 0;
  text-align: left;
  font-size: 12px;
}

.megamenu .nav-menu > li ul.child-nav li > a:hover,
.megamenu .nav-menu > li ul.child-nav li.active > a {
  color: #ec3642;
}

.menu-wrap ul.nav-menu li ul.show {
  border-top: 1px solid rgba(255, 255, 255, 0.35);
}

.menu-wrap ul.nav-menu li li {
  padding-left: 18px;
}

.menu-wrap ul.nav-menu li li > a {
  padding: 6px 0;
}

.link-menu:hover {
