/* Parallax Content */
.parallax-content-wrap {
  height: 100%;
  display: none;
  position: absolute;
  z-index: 3;
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

.parallax-content {
  height: 100%;
  position: relative;
  width: 100%;
}

.parallax-row {
  float: left;
}

.parallax-col-box {
  float: left;
}

