  height: 455px;
  width: 218px;
}

.parallax-col .thumb.w3 img, .parallax-col .thumb.w2 img {
  width: 100%;
  height: 100%;
}

.parallax-col .parallax-row .thumb img {
  width: calc(100% + 50px);
  transition: all 0.45s ease 0s;
  -o-transition: all 0.45s ease 0s;
  -ms-transition: all 0.45s ease 0s;
  -moz-transition: all 0.45s ease 0s;
  -webkit-transition: all 0.45s ease 0s;
  filter: url("filters.svg#grayscale");
}

.parallax-col .parallax-row .thumb:hover img {
  filter: none;
}

.parallax-col .parallax-row .thumb {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.parallax-col .parallax-row .thumb:before {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  content: '';
  right: 0;
  transition: all 0.45s ease 0s;
  -o-transition: all 0.45s ease 0s;
  -ms-transition: all 0.45s ease 0s;
  -moz-transition: all 0.45s ease 0s;
  -webkit-transition: all 0.45s ease 0s;
  opacity: 0;
  background: rgba(18, 18, 18, 0.33);
  z-index: 998;
}

.parallax-col .parallax-row .thumb:hover:before {
  opacity: 1;
}

.parallax-col .parallax-row .thumb:after {
  background: #ec3642;
  content: '\f047';
  position: absolute;
  height: 37px;
  width: 37px;
  text-align: center;
  font-size: 18px;
  font-family: FontAwesome;
  left: calc(50% - 19px);
  top: calc(50% - 19px);
  z-index: 1000;
  line-height: 37px;
  opacity: 0;
  transform: translateY(80px);
  -o-transform: translateY(80px);
  -ms-transform: translateY(80px);
  -moz-transform: translateY(80px);
  -webkit-transform: translateY(80px);
  transition: all 0.45s ease 0s;
  -o-transition: all 0.45s ease 0s;
  -ms-transition: all 0.45s ease 0s;
  -moz-transition: all 0.45s ease 0s;
  -webkit-transition: all 0.45s ease 0s;
}

.parallax-col .parallax-row .thumb:hover:after {
  opacity: 1;
  transform: translateY(0);
  -o-transform: translateY(0);
  -ms-transform: translateY(0);
  -moz-transform: translateY(0);
  -webkit-transform: translateY(0);
}

