}

.coming-soon .title {
  border: 2px solid;
  padding: 8px 15px;
  display: inline-block;
  margin-bottom: 7px;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: normal;
}

.coming-soon #scene {
  width: 100%;
  height: 100%;
  background: url("../images/bg-welcome.jpg") repeat scroll top center/cover transparent;
